/* Image Styles */
.wp-block-image.is-style-custom-filter-blackwhite img {
  filter: grayscale(100%);
  transition: filter 0.5s ease-in-out;
}
.wp-block-image.is-style-custom-filter-blackwhite img:hover {
  filter: grayscale(0%);
}
.wp-block-image.is-style-round-logo-border img {
  padding: 10px;
  border: 1px solid #E9E9E9;
  border-radius: 50%;
}

/* Card Styles */
.wp-block-group.osi-card {
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.03);
  margin-bottom: 20px !important;
}
.wp-block-group.osi-card h3 {
  line-height: 30px !important;
}

@media (max-width: 950px) {
  .is-style-mob-2-cols.wp-block-columns {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    max-width: 95% !important;
  }
  .is-style-mob-2-cols.wp-block-columns .wp-block-column {
    max-width: 45vw;
    width: 100%;
    flex-basis: unset !important;
  }
}
@media (max-width: 475px) {
  .is-style-mob-2-cols.wp-block-columns {
    grid-template-columns: repeat(1, 1fr);
  }
  .is-style-mob-2-cols.wp-block-columns wp-block-column {
    max-width: 90vw;
  }
}

/* Slide-Up Animation */
.content .wp-block-heading.slide-up,
.content p.slide-up {
  transform: translateY(20px);
  opacity: 0;
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out, visibility 0s 0.5s;
  overflow: hidden;
}
.content .wp-block-heading.slide-up.visible,
.content p.slide-up.visible {
  transform: translateY(0);
  opacity: 1;
  overflow: hidden;
}

.is-style-spin-green > a,
.is-style-spin-white > a,
.is-style-spin-green.block-editor-block-list__block > div,
.is-style-spin-white.block-editor-block-list__block > div {
  padding: 19px 40px !important;
  font-size: 16px !important;
  line-height: 19px;
  font-weight: 600 !important;
  background-color: #1B1B1B !important;
  border-radius: 100px;
  display: inline-block;
  vertical-align: middle;
  transform: perspective(1px) translateZ(0);
  position: relative;
  transition-property: color;
  transition-duration: 0.3s;
  min-width: max-content;
  transition: none;
  box-sizing: border-box;
  border-color: transparent !important;
}

.is-style-spin-green > a,
.is-style-spin-green.block-editor-block-list__block > div {
  color: #fff !important;
  text-decoration: none;
}

.is-style-spin-white > a,
.is-style-spin-white.block-editor-block-list__block > div {
  color: #1B1B1B !important;
  text-decoration: none;
}

.is-style-spin-green.block-editor-block-list__block > div::before,
.is-style-spin-white.block-editor-block-list__block > div::before,
.is-style-spin-green > a::before,
.is-style-spin-white > a::before {
  content: "";
  background: #3DA639;
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transform: scaleX(1);
  transform-origin: 50%;
  transition-property: transform;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
  border-radius: 100px;
  border-width: 0px;
  box-sizing: border-box;
  border-color: transparent !important;
  inset: -2px;
}

.is-style-spin-green > a::before,
.is-style-spin-green.block-editor-block-list__block > div::before {
  background: #3DA639;
}

.is-style-spin-white > a::before,
.is-style-spin-white.block-editor-block-list__block > div::before {
  background: #fff;
  color: black;
}

.is-style-spin-green > a:hover::before,
.is-style-spin-white > a:hover::before,
.is-style-spin-green.block-editor-block-list__block > div:hover::before,
.is-style-spin-white.block-editor-block-list__block > div:hover::before {
  transform: scaleX(0);
}

.is-style-spin-green > a:hover,
.is-style-spin-white > a:hover,
.is-style-spin-green.block-editor-block-list__block > div:hover,
.is-style-spin-white.block-editor-block-list__block > div:hover {
  box-sizing: border-box !important;
  border-width: 0 !important;
  text-decoration: underline !important;
}

.is-style-spin-white > a:hover,
.is-style-spin-white.block-editor-block-list__block > div:hover {
  color: white !important;
}

